.body{
  margin: 0;
  min-height: 100vh;
  background: linear-gradient(45deg, pink, orange);
  overflow-x: none;
}
.App{
  padding: 3em 0 0 0;
}
header{
  display: flex;
  justify-content: center;
  width: 100vw;
}
header div{
  border-radius: 20px;
  background: white;
  display: flex;
  flex-direction: column;

}
.title{
  display: inline;
  margin: 0 ;
  color:rgba(247, 139, 90, 0.856);
  font-size: 3em;
  padding: 0.5em;
  text-align: center;
  text-transform: uppercase;
}
h4{
  margin: 0;
  text-align: center;
  color: gray;

}
h1{
  text-align: center;
  padding: 0 10px;
}
.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 1em 0 ;
}
.search-bar{
  width: 50%;
  border: 1px solid gray;
  padding: 10px;
 
}
.search-button{
  background: rgba(247, 139, 90, 0.856);
  border: none;
  padding: 12px 20px;
  text-transform: uppercase;
  color: white;
}
.recipe{
  display: flex;
  justify-content: space-around;
flex-wrap: wrap;
background-color: white;
margin: 10vh;
}
.container{
  display: flex;
  justify-content: center;
  width: 100vw;
  flex-flow: row wrap;
}