.recipe{
    border-radius: 10px;
    box-shadow: 0px 5cp 20px #dedede;
    margin: 20px;
    display: flex;
    flex-direction: column;
    background: white;
    align-items: center;
    max-width: 350px;
}
.recipe:hover{
    box-shadow: 0px 10px 20px 0px rgb(139, 74, 49);
    transition: transform 0.5s ease-in-out;
    transform: translateY(-10px);
}
.image{
  width: 100%;
}